import {Component} from '@angular/core';
import {IonicPage, NavController, NavParams} from 'ionic-angular';


@IonicPage()
@Component({
  selector: 'page-view-account-information02',
  templateUrl: 'view-account-information02.html',
})
export class ViewAccountInformation02Page {
  accountDetails;
  accountInfo=[];

  constructor(public navCtrl: NavController,
              public navParams: NavParams) {
    this.accountDetails = JSON.parse(this.navParams.get("accountDetails"));
    for(let key in this.accountDetails){
      if(this.accountDetails[key] != null) {
        this.accountInfo.push({
          key: key,
          value: this.accountDetails[key]
        });
      }
    }
  }

  ionViewDidLoad() {

  }

}



